import React from 'react';
import PropsType from 'prop-types';
import PropsTypes from "prop-types";
import "./Movie.css"

class Movie1 extends React.Component {
    constructor(props) {
        super(props);
    }

    render() {
        const {title, year, summary, poster, genres} = this.props;
        return (
            <div className="movie">
                <img src={poster} alt={title} title={title}/>
                <div className="movie__data">
                    <h3 className="movie__title">{title}</h3>
                    <h5 className="movie__year">{year}</h5>
                    <ul className="movie__genres">{genres.map((genre, idx) => (
                        <li id={idx} className="genre">{genre}</li>
                    ))}</ul>
                    <p className="movie__summary">{summary}</p><br></br><br></br><br></br>
                </div>
            </div>
        )
    }
}

Movie1.propsType = {
    id: PropsTypes.number.isRequired,
    year: PropsTypes.number.isRequired,
    title: PropsTypes.string.isRequired,
    summary: PropsTypes.string,
    poster: PropsTypes.string,
    geners: PropsTypes.arrayOf(PropsTypes.string)
}

export default Movie1;